/* eslint-disable @typescript-eslint/no-unsafe-assignment */
import React, { useState } from "react";
import { Grid, Button } from "@material-ui/core";
import { Field, Formik, Form } from "formik";
import { TextField, EntryOption } from "./entryFormField";
import { EntryType } from "../types";

export type newEntry = {
  date: string
  specialist: string,
  description: string,
  type: EntryType
  healthCheckRating: string,  // <-- MIT HELVETTI?
  Criteria: string,
  employername: string,
  sickLeave: string,
};

interface Props {
  onSubmit: (values: newEntry) => void;
  onCancel: () => void;
}

export const EntryOptions: EntryOption[] = [
  { value: EntryType.HealthCheck, label: "HealthCheck" },
  { value: EntryType.Hospital, label: "Hospital" },
  { value: EntryType.OccupationalHealthcare, label: "OccupationalHealthcare" },
];

export const AddEntryForm = ({ onSubmit, onCancel }: Props) => {
  const [type, setType] = useState('HealthCheck');
  

  return (
    <Formik
      initialValues={{
        date: "",
        specialist: "",
        description: "",
        type: EntryType.HealthCheck,
        healthCheckRating: "",  // <-- MIT HELVETTI?
        Criteria: "",
        employername: "",
        sickLeave: "",
      }}
      onSubmit={onSubmit}
      validate={(values) => {
        const requiredError = "Field is required";
        const errors: { [field: string]: string } = {};
        if (!values.date) {
          errors.date = requiredError;
        }
        if (!values.specialist) {
          errors.specialist = requiredError;
        }
        if (!values.description) {
          errors.description = requiredError;
        }
        return errors;
      }}
    >
      {({ isValid, dirty }) => {
        //console.log(values);
        return (
          <Form className="form ui">
            Type
            <br/>
             <select name="type" onChange={(e) => setType(e.target.value)}>
                  {EntryOptions.map(o => <option key={o.value} value={o.value}>{o.label}</option>)}
            </select>
            <Field
              label="Date"
              placeholder="2022-09-10"
              name="date"
              component={TextField}
            />
            <Field
              label="Specialist"
              placeholder="Name"
              name="specialist"
              component={TextField}
            />
            <Field label="Description" placeholder="text...." name="description" component={TextField} />

            {type === "HealthCheck" ? <Field label="Health Check Rating" placeholder="1,2,3,4?" name="healthCheckRating" component={TextField} />: null}
            {type === "Hospital" ? <Field label="Criteria" placeholder="criteria" name="Criteria" component={TextField} />: null}
            {type === "OccupationalHealthcare" ? <Field label="Employer Name" placeholder="paul" name="employername" component={TextField} />: null}
            {type === "OccupationalHealthcare" ? <Field label="Sick Leave" placeholder="Start Date - End Date" name="sickLeave" component={TextField} />: null}
            
            <Grid>
              <Grid item>
                <Button
                  color="secondary"
                  variant="contained"
                  style={{ float: "left" }}
                  type="button"
                  onClick={onCancel}
                >
                  Cancel
                </Button>
              </Grid>
              <Grid item>
                <Button
                  style={{
                    float: "right",
                  }}
                  type="submit"
                  variant="contained"
                  disabled={!dirty || !isValid}
                >
                  Add
                </Button>
              </Grid>
            </Grid>
          </Form>
        );
      }}
    </Formik>
  );
};

export default AddEntryForm;
