/* eslint-disable @typescript-eslint/no-unsafe-call */
/* eslint-disable @typescript-eslint/no-unsafe-assignment */

import React from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import { apiBaseUrl } from "../constants";
import { Diagnosis, Entry, Patient } from "../types";
import LocalHospitalIcon from '@mui/icons-material/LocalHospital';
import HealingIcon from '@mui/icons-material/Healing';
import VaccinesIcon from '@mui/icons-material/Vaccines';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Avatar from '@mui/material/Avatar';
import { Button } from "@material-ui/core";
import AddEntryModal from "../AddEntryModal";
import { newEntry } from "../AddEntryModal/AddEntryForm";



const EntryDetails: React.FC <{entry :Entry}> = ({entry}) => {
    switch (entry.type) {
        case 'Hospital':
            return (
            <ListItem>
                <ListItemAvatar>
                  <Avatar>
                    <LocalHospitalIcon />
                  </Avatar>
                </ListItemAvatar>
                <ListItemText primary={entry.description} secondary={entry.date + " " + entry.specialist} />
              </ListItem>
            );
        case 'OccupationalHealthcare':
            return (
            <ListItem>
                <ListItemAvatar>
                  <Avatar>
                    <VaccinesIcon />
                  </Avatar>
                </ListItemAvatar>
                <ListItemText primary={entry.description} secondary={entry.date + " " + entry.specialist} />
              </ListItem>
            );
        case 'HealthCheck':
            return (
                <ListItem>
                    <ListItemAvatar>
                      <Avatar>
                        <HealingIcon />
                      </Avatar>
                    </ListItemAvatar>
                    <ListItemText primary={entry.description} secondary={entry.date + " " + entry.specialist} />
                  </ListItem>
                );
        default:
            return <div/>;
    }
};


export const PatientInfo = () => {
    const [modalOpen, setModalOpen] = React.useState<boolean>(false);
    const params = useParams();
    const [patientinfo, setPatient] = React.useState<any>([]);
    const [patientEntries, setEntries] = React.useState<any>([]);
    const [diagnoses, setDiagnoses] = React.useState<any>([]);
    const [error, setError] = React.useState<string>();
    
    const openModal = (): void => setModalOpen(true);
    const closeModal = (): void => {
        setModalOpen(false);
      };

    const evalValues = (values: newEntry): any | undefined =>{
        switch (values.type) {
            case "HealthCheck":
                const newObject = {
                UserId : patientinfo.id,
                entry : {
                    date: values.date,
                    specialist: values.specialist,
                    description: values.description,
                    healthCheckRating: parseInt(values.healthCheckRating),
                    type: values.type,
                    id: ""
                }
            };
                return newObject;

            case "Hospital":
                const newObject1 = {
                    UserId : patientinfo.id,
                    entry : {
                    date: values.date,
                    specialist: values.specialist,
                    description: values.description,
                    criteria: values.Criteria,
                    type: values.type,
                    id: ""
                }
            };
                return newObject1;

            case "OccupationalHealthcare":
                const newObject2 = {
                    UserId : patientinfo.id,
                    entry : {
                    date: values.date,
                    specialist: values.specialist,
                    description: values.description,
                    employername: values.employername,
                    SickLeave: values.sickLeave,
                    type: values.type,
                    id: ""
                }
            };
                return newObject2;


            default: return undefined;
        }
    };

      // eslint-disable-next-line @typescript-eslint/require-await
      const submitNewEntry = async (values: newEntry) => {


        try {
            const newObject = evalValues(values);
            console.log(newObject);
            const { data: newEntry } = await axios.post<Entry>(
            `${apiBaseUrl}/patients/${patientinfo.id}/entries`,
            newObject
          );
          patientinfo.entries.push(newEntry);
          closeModal();
          window.location.reload();
        } catch (e: unknown) {
          if (axios.isAxiosError(e)) {
            console.error(e?.response?.data || "Unrecognized axios error");
            setError(String(e?.response?.data?.error) || "Unrecognized axios error");
          } else {
            console.error("Unknown error", e);
            setError("Unknown error");
          }
        }
      };

    React.useEffect(() => {
        const id = params.id;
        const fetchPatient = async () => {
            const results = await axios.get<Patient[]>(`${apiBaseUrl}/patients/${id}`);
            setPatient(results.data[0]);
            setEntries(results.data[0].entries);
        };
            
        const fetchDiagnoses = async () => {
            const results1 = await axios.get<Diagnosis[]>(`${apiBaseUrl}/diagnoses/`);
            setDiagnoses(results1.data);
        };

        void fetchPatient().catch(console.error);
        void fetchDiagnoses().catch(console.error);
        
        }, []);
    
    
    const patientcodes = patientEntries.map((entry: Entry) => entry.diagnosisCodes);
    
    let patientDiagnoses = [];
    
    if (patientcodes.length > 0 && patientcodes[0] !== undefined) {


    // eslint-disable-next-line @typescript-eslint/no-unsafe-return
    patientDiagnoses = diagnoses.filter((diagnose: Diagnosis) => patientcodes[0].includes(diagnose.code.toString()));
    } else {
        patientDiagnoses = [{code: "None", name: "No Diagnoses"}];
    }

    return (
    <div>
        <h1>Name: {patientinfo.name}</h1> 
        <h2>Gender: {patientinfo.gender}</h2>
        <h2>ssh: {patientinfo.ssn}</h2>
        <h2>Occupation: {patientinfo.occupation}</h2>
        <h1>Entries:</h1>
        <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
        {patientEntries.map((entry: Entry) => <EntryDetails key={entry.id} entry = {entry}/>)}
        </List>
        <Button variant="contained" onClick={() => openModal()}>
        Add New Entry
        </Button>
        <AddEntryModal
        modalOpen={modalOpen}
        onSubmit={submitNewEntry}
        error={error}
        onClose={closeModal}
        />
        {error}
        <h2>Diagnoses: </h2>
        {patientDiagnoses.map((diagnose: Diagnosis) => <li key={diagnose.code}>{diagnose.code} : {diagnose.name}</li>)}
        
        
    </div>);
};